<template>
  <div id="mine">
    <v-picture :picSrc="user.picture?user.picture:''" @clickPicture="login" class="user-picture"></v-picture>
    <div v-if="user.name" class="user-name">{{user.name}}</div>
  </div>
</template>

<script>
  export default {
    name: 'mine',
    props: {
      user: {
        type: Object,
        default: null
      }
    },
    methods: {
      login () {
        if (!sessionStorage.getItem('iCainBlogUserInfo')) {
          this.$router.push('/login')
        }
      }
    },
    components: {}
  }
</script>

<style lang="scss" scoped>
#mine {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #F5F5F5;
  .user-picture {
    width: 40%;
    border-radius: 50%;
  }
  .user-name {
    margin-top: 20px;
    font-size: 20px;
  }
}
</style>



